form {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 30px;
  width: 70%;
}

form * {
  height: 40px;
}

.suggestion {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
  text-align: left;
  background-color: #fff;
  border: none;
  border-radius: 10px;
  font-size: 30px;
  width: 85%;
}

.suggestion-text {
  -webkit-appearance:none;
  font: 400 11px system-ui;
  color: #D3D3D3;
  font-size: 30px;
  margin-top: 3px;

}

.search-input {
  -webkit-appearance:none;
  position: absolute;
  border: 3px solid #000;
  border-radius: 10px;
  -webkit-box-shadow: -1px 0 1px 1px #999 inset;
          box-shadow: -1px 0 1px 1px #999 inset;
  font-size: 30px;
  padding-right: 10px;
  background-color: transparent;
  width: 60%;
}

.search-button {
  -webkit-box-shadow: -1px 0 1px 1px #999 inset;
          box-shadow: -1px 0 1px 1px #999 inset;
  position: absolute;
  right: 20%;
  border: 3px solid #000;
  border-left: none;
  border-radius: 0 10px 10px 0;
  width: 50px;
}

.search-button:hover {
  background-color: #ccc;
}

.search-button:active {
  background-color: #808080;
}

.search-button-icon {
  height: 20px;
  width: auto;
}




